@import "_variables";

.home{
  width: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  &-head{
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    &--information{
      width: 100%;
      height: 80px;
      @include center;
      background-color: $black-light;
      .container{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
      }
      .logotype{
        @include center;
      }
      .navigation{
        height: 100%;
        @include center;
        &-list{
          height: 100%;
          @include center;
          margin-right: 40px;
          &--item{
            height: 100%;
            @include center;
            padding: 0 20px;
            background-color: transparent;
            transition: $anim;
            &:hover{
              background-color: #1F1F1F;
            }
            p{
              color: $white;
              font-size: 14px;
              text-transform: uppercase;
              margin: 0;
              transition: $anim;
            }
          }
        }
        &-links{
          @include center;
          a{
            @include center;
            margin-right: 40px;
            &:last-child{
              margin-right: 0;
            }
          }
        }
      }
    }
  }
  &-items{
    width: 100%;
    max-width: 100%;
    @include center;
    padding: 100px 0;
    .container{
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .title-block{
      margin-bottom: 80px;
    }
    &--rows{
      width: calc(100% + 24px);
      display: flex;
      flex-wrap: wrap;
      margin: 0 -12px;
    }
  }
  &-test{
    width: 100%;
    max-width: 100%;
    @include center;
    padding: 50px 0;
    .container{
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    .title-block{
      margin-bottom: 40px;
    }
    &--block{
      width: 100%;
      max-width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      span{
        color: $white;
        font-size: 22px;
        line-height: 28px;
      }
      ol{
        li{
          color: $white;
          font-size: 22px;
          line-height: 28px;
        }
      }
      .vue-btn{
        @include center;
        padding: 10px 20px;
        margin: 10px 0;
        cursor: pointer;
        border: 1px solid $white;
      }
    }
  }
}