// ===================colors=====================
$white: #fff;
$black: #000;
$black-light: #0F0F0F;

$anim: all .2s ease-in-out;
// ===================fonts=====================
@import url('https://fonts.googleapis.com/css2?family=PT+Sans:wght@300;400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@300;400;700&display=swap');
$sans: 'Pt Sans', sans-serif;
$play: 'Playfair Display', sans-serif;

@mixin ie {
    @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {  
        @content;
    }
}
// ========================mixin==============================
@function strip-unit($value) {
    @return $value / ($value * 0 + 1);
}
  
@mixin fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
    $u1: unit($min-vw);
    $u2: unit($max-vw);
    $u3: unit($min-font-size);
    $u4: unit($max-font-size);

    @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
            & {
            font-size: $min-font-size;
            @media screen and (min-width: $min-vw) {
                font-size: calc(#{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} * ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)}));
            }
            @media screen and (min-width: $max-vw) {
                font-size: $max-font-size;
            }
        }
    }
}

@mixin text-limit($row) {
    display: -webkit-box;
    text-overflow: ellipsis;
    overflow: hidden;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: $row;
}
@mixin triangle($color){
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 34px 15px 0;
    border-color: $color transparent transparent transparent;
}
@mixin fixInputIOS{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}
@mixin anyCardHover {
    transition: box-shadow .3s;
    // ------------off hover-------------
    @include offHoverOnDevices{
        &:hover{
            z-index: 1;
            box-shadow: 0 8px 24px rgba(154, 156, 166, 0.3);
        }
    }
}

@mixin center{
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin offScrollBar {
    
    // for chrome
    &::-webkit-scrollbar-track{
        -webkit-box-shadow: inset 0 0 0 rgba(0,0,0,0);
        background-color: rgba(0,0,0,0);
        border-radius: 0;
    }

    &::-webkit-scrollbar{
        width: 0;
        background-color: transparent;
        border-radius: 0;
    }

    &::-webkit-scrollbar-thumb{
        background-color: rgba(0,0,0,0);
        border-radius: 0;
    }
    //for firefox
    scrollbar-color: rgba(0,0,0,0) rgba(0,0,0,0);
    scrollbar-width: none;
}

@mixin styleScrollBar($color-thumb,$color-track,$scrollbar-width) {
    overflow-y: auto;
    // for chrome
    &::-webkit-scrollbar-track{
        -webkit-box-shadow: inset 0 0 $scrollbar-width rgba(0,0,0,0.3);
        background-color: $color-track;
        border-radius: 4px;
    }

    &::-webkit-scrollbar{
        width: $scrollbar-width;
        background-color: transparent;
        border-radius: 4px;
    }

    &::-webkit-scrollbar-thumb{
        background-color: $color-thumb;
        border-radius: 4px;
    }
    //for firefox
    scrollbar-color: $color-thumb $color-track;
    scrollbar-width: thin;
}

@mixin w($size, $sizeMin:0px){
    @if ($sizeMin == 0){
        @media (max-width: ($size)) { @content; }
    } @else {
        @media (min-width: $sizeMin) and (max-width: ($size)){ @content; }
    }
}

@mixin h($size){
    @media (max-height: $size) { @content; }
}

@mixin offHoverOnDevices {
    @media screen and (min-width: 1200px) {  @content;  }
}

@mixin tableColWidth($sizes...){
    @for $i from 0 to length($sizes) {
        &:nth-child(#{$i + 1}) {
            width: #{nth($sizes, $i + 1)}
        }
    }
}

@mixin minw($size) {
    @media (min-width: $size) { @content; }
}