*,
*:before,
*:after {
  box-sizing: inherit; }

* {
  padding: 0;
  margin: 0; }

html {
  box-sizing: border-box;
  line-height: 1.15;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

ul,
ol {
  padding: 0;
  margin: 0;
  list-style-type: none; }

p,
dd,
dl,
figure,
blockquote {
  margin: 0; }

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit; }

audio,
