@import "_variables";

.footer{
  width: 100%;
  max-width: 100%;
  height: 50px;
  @include center;
  background-color: $black-light;
  .container{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-copyright{
    @include center;
    p{
      color: $white;
      font-size: 12px;
      line-height: 16px;
    }
  }
  &-poweredBy{
    @include center;
    a{
      color: $white;
      font-size: 12px;
      line-height: 16px;
      font-weight: 700;
    }
  }
}
