@import "_variables";

.header{
  width: 100%;
  height: 44px;
  @include center;
  .container{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &-information{
    @include center;
    &--item{
      position: relative;
      @include center;
      margin-right: 20px;
      &:after{
        content: '|';
        position: absolute;
        top: 0;
        right: -11px;
        color: $white;
        font-size: 12px;
        line-height: 16px;
      }
      &:last-child{
        margin-right: 0;
        &:after{
          display: none;
        }
      }
      img{
        margin-right: 16px;
      }
      p{
        color: $white;
        font-size: 12px;
        line-height: 16px;
      }
    }
  }
  &-login{
    @include center;
    img{
      margin-right: 12px;
    }
    a, p{
      color: $white;
      font-size: 12px;
      line-height: 16px;
    }
    p{
      margin: 0 5px;
    }
  }
}