@import "variables";

// === main ===
body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    color: $black;
    font-weight: 400;
    font-family: $sans;
    background-color: $black;
    @include ie {
        margin-top: 150px;
    }
}
main{
    width: 100%;
    max-width: 1920px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
input{
    -moz-appearance: textfield;
    @include fixInputIOS;
    &::-webkit-input-placeholder {
        color: $white;
    }
    &::-moz-placeholder {
        color: $white;
    }
    &:-ms-input-placeholder {
        color: $white;
    }
    &:-moz-placeholder {
        color: $white;
    }
}
.container {
    width: 100%;
    max-width: 1250px;
    display: flex;
    padding: 0;
    margin: 0 auto;
    @include w(1250px){
        max-width: 100%;
        padding: 0 10px;
    }
}
// === main ===

// === title ===
.title-block{
    display: flex;
    flex-direction: column;
    align-items: center;
    &--text{
        @include center;
        margin-bottom: 40px;
        p{
            color: $white;
            font-size: 36px;
            line-height: 48px;
            font-weight: 700;
            font-family: $play;
            text-transform: uppercase;
        }
    }
    &--line{
        width: 120px;
        height: 1px;
        background-color: $white;
    }
}
// === title ===

// === item ===
.item{
    width: calc((100% / 4) - 24px);
    display: flex;
    flex-direction: column;
    margin: 0 12px;
    margin-bottom: 40px;
    &-img{
        width: 100%;
        height: 325px;
        @include center;
        margin-bottom: 25px;
        background-color: $black-light;
        img{
            width: auto;
            max-width: 100%;
            height: auto;
            max-height: 100%;
        }
    }
    &-text{
        width: 100%;
        max-width: 100%;
        display: flex;
        flex-direction: column;
    }
    &-name{
        max-width: 100%;
        max-height: 24px;
        color: $white;
        font-size: 18px;
        line-height: 24px;
        font-family: $play;
        overflow: hidden;
        margin-bottom: 10px;
    }
    &-price{
        color: #D0D0D0;
        font-size: 16px;
        line-height: 21px;
        font-weight: 700;
        font-family: $play;
    }
}
// === item ===

// === PAGES PAGE ===
.heading {
    font-style: normal;
    font-weight: 500;
    font-size: 26px;
    line-height: 130%;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 0.5em;
}
.subheading {
    font-weight: 500;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: $black;
    margin-bottom: 1.5em;
}
.pages-list {
    text-align: center;
    margin-bottom: 80px;
    li {
        margin-bottom: 1em;
        a {
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 15px 0;
            &:hover span {
                border-bottom-color: transparent;
            }
            &.done svg {
                color: green;
            }
            &.undone svg {
                color: red;
            }
        }
        span {
            border-bottom: 1px solid;
            margin-right: 10px;
            transition: .3s;
        }
        svg {
            font-size: 30px;
            color: #fff;
        }
    }
}
// === PAGES PAGE ===